import { Character, AttackResult } from "shared";

interface ResolveAttackParams {
  baseDamage: number;
  characterAccuracy: number;
  moveAccuracy: number;
  critChance: number;
}

export const resolveAttack = ({
  baseDamage,
  characterAccuracy,
  moveAccuracy,
  critChance,
}: ResolveAttackParams): AttackResult => {
  const finalAccuracy = characterAccuracy * moveAccuracy;
  const missChance = Math.round(Math.random() * 100) / 100;

  if (missChance > finalAccuracy) {
    return {
      damage: 0,
      missed: true,
      isCritical: false,
    };
  }

  const isCritical = Math.random() < critChance;
  const critMultiplier = isCritical ? 1.5 : 1;

  return {
    damage: Math.max(0, Math.round(baseDamage * critMultiplier)),
    missed: false,
    isCritical,
  };
};

export const charge = (receiver: Character) => {
  return 50;
};

export const getAnnouncerMessage = (
  attackerName: string,
  actionName: string,
  result: AttackResult
) => {
  if (result.missed) {
    return `${attackerName}'s ${actionName} missed!`;
  }

  if (result.isCritical) {
    return `Critical hit! ${attackerName}'s ${actionName} was devastating!`;
  }

  return `${attackerName} used ${actionName}!`;
};

export const wait = (ms: number) =>
  new Promise<void>((resolve) => {
    setTimeout(() => {
      resolve();
    }, ms);
  });
